import React from "react";
// import * as Fui from "@fluentui/react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { RoutedComponentBase } from "../Base/ComponentBase";

class PrivateIssues extends RoutedComponentBase {
	public constructor(props: RouteComponentProps) {
		super(props);
	}

	public render(): React.ReactNode {
		return this.props.match.params.issueId;
	}
}

export const Issues = withRouter(PrivateIssues);
